@mixin breakpoint($point) {
  @if $point == "desktop" {
    @media (min-width: 67em) {
      @content;
    }
  } @else if $point == "tablet" {
    @media (min-width: 50em) {
      @content;
    }
  } @else if $point == "mobile" {
    @media (min-width: 37.5em) {
      @content;
    }
  }
}
